* {
	margin: 0;
	padding: 0;
}

body {
	font-family: Helvetica, Arial, sans-serif;
	color: #4a4a4a;
}

h1 {
	margin-top: 100px;
	text-transform: uppercase;
	display: block;
}

.main-container {
	text-align: center;
	margin: 10px;
}

.search input {
	margin: 50px;
	width: 100px;
}

.main-wrap {
	margin: 0 auto;
	padding: 0px 15px;
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
	grid-template-rows: repeat(10, 280px);
	grid-auto-rows: 250px;
	grid-gap: 20px;
}

.state-item {
	background-color: #f9f9f9;
	border: 1px solid rgba(19, 19, 19, 0.11);
	border-radius: 7px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;

	font-size: 0.85em;
}

img,
p,
h3 {
	padding: 10px;
}
